"use client"

import { useState, useEffect } from "react"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table"
import { Loader2, Store, Package, LayoutGrid } from "lucide-react"

interface StoreData {
  store: string
  items: { [key: string]: number }
  total: number
}

export default function DashboardView() {
  const [selectedPeriod, setSelectedPeriod] = useState("2025-11")
  const [data, setData] = useState<StoreData[]>([])
  const [loading, setLoading] = useState(false)
  const [allItems, setAllItems] = useState<string[]>([])

  // 2025年11月から2030年12月までの期間を生成
  const generatePeriods = () => {
    const periods = []
    for (let year = 2025; year <= 2030; year++) {
      const endMonth = year === 2030 ? 12 : 12
      const startMonth = year === 2025 ? 11 : 1
      for (let month = startMonth; month <= endMonth; month++) {
        periods.push(`${year}-${String(month).padStart(2, "0")}`)
      }
    }
    return periods
  }

  const periods = generatePeriods()

  useEffect(() => {
    fetchData()
  }, [selectedPeriod])

  const fetchData = async () => {
    setLoading(true)
    try {
      const response = await fetch(`/api/dashboard?period=${selectedPeriod}`)
      const result = await response.json()

      if (result.error || !Array.isArray(result)) {
        console.error("[v0] API error:", result.error || "Invalid response format")
        setData([])
        setAllItems([])
        return
      }

      const itemsSet = new Set<string>()
      result.forEach((store: StoreData) => {
        Object.keys(store.items).forEach((item) => itemsSet.add(item))
      })

      setAllItems(Array.from(itemsSet).sort())
      setData(result)
    } catch (error) {
      console.error("[v0] Error fetching data:", error)
      setData([])
      setAllItems([])
    } finally {
      setLoading(false)
    }
  }

  const formatPeriodLabel = (period: string) => {
    const [year, month] = period.split("-")
    return `${year}年${Number.parseInt(month)}月度`
  }

  return (
    <div className="min-h-screen bg-background p-6">
      <div className="mx-auto max-w-7xl space-y-6">
        <div className="flex items-center justify-between rounded-xl bg-primary p-6 shadow-md">
          <div>
            <h1 className="text-3xl font-bold text-primary-foreground">店舗別台数管理ダッシュボード</h1>
            <p className="mt-1 text-primary-foreground/80">各店舗の商品別台数を確認できます</p>
          </div>

          <div className="w-64">
            <Select value={selectedPeriod} onValueChange={setSelectedPeriod}>
              <SelectTrigger className="border-2 border-primary-foreground/20 bg-white text-foreground font-semibold shadow-sm">
                <SelectValue placeholder="期間を選択" />
              </SelectTrigger>
              <SelectContent>
                {periods.map((period) => (
                  <SelectItem key={period} value={period}>
                    {formatPeriodLabel(period)}
                  </SelectItem>
                ))}
              </SelectContent>
            </Select>
          </div>
        </div>

        <div className="grid gap-4 md:grid-cols-3">
          <Card className="border-2 border-primary/30 bg-secondary shadow-sm">
            <CardHeader className="flex flex-row items-center justify-between pb-2">
              <CardTitle className="text-sm font-medium text-foreground/80">総店舗数</CardTitle>
              <Store className="h-5 w-5 text-accent" />
            </CardHeader>
            <CardContent>
              <div className="text-4xl font-bold text-accent">{data.length}</div>
              <p className="text-xs text-muted-foreground mt-1">登録されている店舗</p>
            </CardContent>
          </Card>

          <Card className="border-2 border-primary/30 bg-secondary shadow-sm">
            <CardHeader className="flex flex-row items-center justify-between pb-2">
              <CardTitle className="text-sm font-medium text-foreground/80">総台数</CardTitle>
              <Package className="h-5 w-5 text-accent" />
            </CardHeader>
            <CardContent>
              <div className="text-4xl font-bold text-accent">{data.reduce((sum, store) => sum + store.total, 0)}</div>
              <p className="text-xs text-muted-foreground mt-1">選択期間の合計</p>
            </CardContent>
          </Card>

          <Card className="border-2 border-primary/30 bg-secondary shadow-sm">
            <CardHeader className="flex flex-row items-center justify-between pb-2">
              <CardTitle className="text-sm font-medium text-foreground/80">商品種類数</CardTitle>
              <LayoutGrid className="h-5 w-5 text-accent" />
            </CardHeader>
            <CardContent>
              <div className="text-4xl font-bold text-accent">{allItems.length}</div>
              <p className="text-xs text-muted-foreground mt-1">取り扱い商品</p>
            </CardContent>
          </Card>
        </div>

        <Card className="border-2 border-primary/30 bg-white shadow-sm overflow-hidden">
          <CardHeader className="bg-white border-b-2 border-primary">
            <CardTitle className="text-foreground">{formatPeriodLabel(selectedPeriod)} - 店舗別商品台数一覧</CardTitle>
            <CardDescription className="text-muted-foreground">各店舗の商品別の台数を表示しています</CardDescription>
          </CardHeader>
          <CardContent className="p-0 bg-white">
            {loading ? (
              <div className="flex h-64 items-center justify-center bg-white">
                <Loader2 className="h-8 w-8 animate-spin text-primary" />
              </div>
            ) : data.length === 0 ? (
              <div className="flex h-64 items-center justify-center text-muted-foreground bg-white">
                データが見つかりませんでした
              </div>
            ) : (
              <div className="overflow-x-auto">
                <Table>
                  <TableHeader>
                    <TableRow className="bg-white hover:bg-white border-b-2 border-primary">
                      <TableHead className="font-bold text-foreground">店舗名</TableHead>
                      {allItems.map((item) => (
                        <TableHead key={item} className="text-center font-bold text-foreground">
                          {item}
                        </TableHead>
                      ))}
                      <TableHead className="text-center font-bold text-primary">合計</TableHead>
                    </TableRow>
                  </TableHeader>
                  <TableBody>
                    {data.map((store, index) => (
                      <TableRow key={store.store} className="border-b border-muted hover:bg-muted/50 bg-white">
                        <TableCell className="font-medium text-foreground">{store.store}</TableCell>
                        {allItems.map((item) => (
                          <TableCell key={item} className="text-center text-foreground">
                            {store.items[item] || 0}
                          </TableCell>
                        ))}
                        <TableCell className="text-center font-bold text-primary">{store.total}</TableCell>
                      </TableRow>
                    ))}
                  </TableBody>
                </Table>
              </div>
            )}
          </CardContent>
        </Card>
      </div>
    </div>
  )
}
